@charset "UTF-8";

/* 
Стиль React
BlockName-ElemName_modName_modVal

Имена записываются латиницей.

Имена блоков и элементов пишутся с заглавной буквы. Имена модификаторов — со строчной.

Каждое слово внутри имени пишется с заглавной буквы.

Имя элемента отделяется от имени блока одним дефисом (-).

Разделители имени и значения модификаторов совпадают с классической схемой.
*/

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Regular.ttf");
  font-weight: 400;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.ttf");
  font-weight: 500;
}

@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Bold.ttf");
  font-weight: 700;
}

body {
  font-family: "Roboto", serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
  color: #fcfbf7;
}

body.theme_light {
  color: #261c00;
}

.fontColor_gray {
  color: #918f8a;
}

.theme_light .fontColor_gray {
  color: #bfbfbf;
}

.fontType_headline {
  font-weight: bold;
  font-size: 30px;
  line-height: 32px;
}

.fontType_subhead {
  font-weight: bold;
  font-size: 20px;
  line-height: 22px;
}

.fontType_caption {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
}

.Person-AvatarWrapper {
  display: block;
}

.Person-Avatar {
  border-radius: 50%;
  filter: grayscale(1);
}

.Person-Name {
  text-overflow: ellipsis;
  overflow: hidden;
}

.Person-Value {
  margin-top: 4px;
}

.Person_vertical {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Person_vertical .Person-AvatarWrapper {
  position: relative;
}

.Person_vertical .Person-AvatarWrapper::before {
  display: block;
  content: attr(data-emoji);
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translate(-50%, -100%);
  font-size: 30px;
  line-height: 32px;
  z-index: 5;
}

.Person_vertical .Person-Name {
  max-width: 100px;
  text-align: center;
  margin-top: 2px;
}

.Person_horizontal {
  display: grid;
  grid-template-columns: 40px 182px;
  grid-template-rows: 1fr 1fr;
  column-gap: 14px;
  padding-left: 16px;
}

.Person_horizontal .Person-AvatarWrapper {
  grid-row: 1/3;
}

.Person_horizontal .Person-Avatar {
  width: 40px;
}

.theme_light .Person-Avatar {
  filter: none;
}

.HeaderWrapper {
  margin-top: 20px;
}

.Header {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.Header-Title {
  margin-bottom: 8px;
}

.Divider {
  background: #918f8a;
  opacity: 0.3;
}

.Divider_vertical {
  height: 100%;
  width: 2px;
}

.theme_light .Divider {
  background: #bfbfbf;
}

.LeadersBodyWrapper {
  flex-grow: 1;
  display: flex;
}

.LeadersBody {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: flex-end;
}

.LeadersBody-Pedestals {
  display: flex;
  align-items: flex-end;
}

.LeadersBody-Pedestal_1 {
  z-index: 5;
}

.LeadersBody-Pedestal_2,
.LeadersBody-Pedestal_3 {
  z-index: 3;
  margin-right: -16px;
  margin-left: -16px;
}

.LeadersBody-Pedestal_3 .LeadersBody-PersonWrapper,
.LeadersBody-Pedestal_3 .LeadersBody-PlaceWrapper,
.LeadersBody-Pedestal_5 .LeadersBody-PersonWrapper,
.LeadersBody-Pedestal_5 .LeadersBody-PlaceWrapper {
  padding-right: 16px;
}

.LeadersBody-Pedestal_2 .LeadersBody-PersonWrapper,
.LeadersBody-Pedestal_2 .LeadersBody-PlaceWrapper,
.LeadersBody-Pedestal_4 .LeadersBody-PersonWrapper,
.LeadersBody-Pedestal_4 .LeadersBody-PlaceWrapper {
  padding-left: 16px;
}

.LeadersBody-PersonWrapper {
  margin-bottom: 16px;
}

.LeadersBody-Block {
  text-align: center;
  padding-top: 18px;
  height: 68px;
  width: 120px;
  box-sizing: border-box;
  border-top-left-radius: 6px;
  border-top-right-radius: 6px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) -2.22%, rgba(0, 0, 0, 0.8) 100%), radial-gradient(149.08% 95.38% at 38.75% 71.48%, #000000 0%, #231900 0.01%, #4d4d4d 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.2), inset 2px 2px 16px rgba(103, 103, 103, 0.6);
}

.LeadersBody-Block_1 {
  height: 108px;
  background: radial-gradient(91.67% 122.17% at 69.17% -11.17%, #ffa300 0%, #2d1c00 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.4), inset 0px 6px 15px rgba(255, 162, 0, 0.4);
}

.LeadersBody-Block_2,
.LeadersBody-Block_3 {
  height: 88px;
}

.theme_light .LeadersBody-Block {
  background: linear-gradient(180deg, rgba(244, 244, 244, 0.9) 0.82%, #e9e9e9 100%, rgba(234, 234, 234, 0) 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0px 0px 20px rgba(207, 207, 207, 0.5);
}

.theme_light .LeadersBody-Block_1 {
  background: linear-gradient(180deg, #fff2d1 0.82%, #ffd66c 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0px 0px 20px rgba(255, 176, 57, 0.8);
}

.VoteBodyWrapper {
  flex-grow: 1;
  display: flex;
}

.VoteBody {
  flex-grow: 1;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.VoteBody-Person {
  box-sizing: border-box;
  border-radius: 6px;
  padding-top: 20px;
  min-width: 104px;
  min-height: 142px;
}

.VoteBody-Person:hover {
  background: radial-gradient(85.62% 148.33% at 49.85% 100%, rgba(68, 58, 42, 0.7864) 0%, rgba(0, 0, 0, 0.8) 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.2), inset 2px 2px 16px rgba(255, 255, 255, 0.1);
}

.VoteBody-Person[data-isSelected="true"] {
  background: radial-gradient(91.67% 122.17% at 69.17% -11.17%, #ffa300 0%, #2d1c00 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.4), inset 0px 6px 15px rgba(255, 162, 0, 0.4);
}

.VoteBody-PersonsGroup .VoteBody-Person:nth-child(1) {
  margin-bottom: 4px;
}

.VoteBody-Button:nth-child(1) {
  margin-bottom: 66px;
}

.VoteBody-Button::before {
  display: block;
  content: url("/images/button-dark.svg");
  width: 64px;
  height: 64px;
}

.VoteBody-Button_top::before {
  transform: rotate(180deg);
}

.VoteBody-Button:hover::before,
.VoteBody-Button_disabled::before {
  content: url("images/button-hover-dark.svg");
}

.theme_light .VoteBody-Person:hover {
  background: linear-gradient(180deg, rgba(244, 244, 244, 0.9) 0.82%, #e9e9e9 100%, rgba(234, 234, 234, 0) 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0px 0px 20px rgba(207, 207, 207, 0.5);
}

.theme_light .VoteBody-Person[data-isSelected="true"] {
  background: linear-gradient(180deg, #fff2d1 0.82%, #ffd66c 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0px 0px 20px rgba(255, 176, 57, 0.8);
}

.theme_light .VoteBody-Button::before {
  content: url("/images/button-hover-light.svg");
}

.theme_light .VoteBody-Button:hover::before,
.theme_light .VoteBody-Button_disabled::before {
  content: url("images/button-light.svg");
}

.ChartBodyWrapper {
  flex-grow: 1;
  display: flex;
}

.ChartBody {
  flex-grow: 1;
  display: flex;
  justify-content: center;
}

.ChartBody-InternalWrapper {
  display: flex;
  flex-direction: column;
  width: 552px;
  margin: 32px auto 0;
}

.ChartBody-Columns {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  height: 180px;
}

.ChartBody-Users {
  display: flex;
  align-items: flex-end;
  box-sizing: border-box;
  justify-content: center;
  padding-top: 24px;
}

.Column {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  height: 100%;
  width: 40px;
}

.Column-Value,
.Column-Title {
  text-align: center;
}

.Column-Line {
  border-radius: 6px;
  min-height: 8px;
  margin-top: 8px;
  margin-bottom: 12px;
}

.Column-Line_active_false {
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.8) 100%), radial-gradient(149.08% 93.3% at 38.75% 72.1%, #000000 0%, #231900 0.01%, #4d4d4d 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.2), inset 2px 2px 16px rgba(103, 103, 103, 0.6);
}

.Column-Line_active_true {
  background: radial-gradient(81.14% 100% at 50% 0%, #ffa300 0%, #2d1c00 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.4), inset 0px 6px 15px rgba(255, 162, 0, 0.5);
}

.theme_light .Column-Line_active_false {
  background: linear-gradient(180deg, rgba(244, 244, 244, 0.9) 0.82%, #e9e9e9 100%, rgba(234, 234, 234, 0) 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0px 0px 20px rgba(207, 207, 207, 0.5);
}

.theme_light .Column-Line_active_true {
  background: linear-gradient(180deg, #fff2d1 0.82%, #ffd66c 100%);
  box-shadow: inset -1px 1px 1px rgba(255, 255, 255, 0.5), inset 0px 0px 20px rgba(255, 176, 57, 0.8);
}

* {
  margin: 0;
  padding: 0;
}

body {
  display: flex;
  flex-direction: column;
  height: 100vh;
  background: radial-gradient(70.24% 70.24% at 49.87% 29.76%, rgba(19, 14, 0, 0.9) 0%, #000000 100%);
}

body.theme_light {
  background: #fff;
}